.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.AppBlock {
  display: flex;
  flex-direction: column;
  gap: 8px;
  justify-content: center;
  align-items: center;

}

.Names {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
} 

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




.ImageViewer {
  width: 100%;
  max-width: 700px;
  outline: 1px solid;
}